import React, { Component } from "react";
import Link from "next/link";
import "../styles/styles.css";

class Header extends Component {
  state = {};
  render() {
    return (
      <div className="container">
        <header>
          <div id="avatar">
            <img src="../static/Jesal.jpg" alt="Avatar" />
          </div>
        </header>
      </div>
    );
  }

  handleClick = () => {
    <Link href="/">
      <a>Home</a>
    </Link>;
  };
}

export default Header;
